.progress {
  .container();
}

.progress__wrapper {
  padding: 75px;
  display: flex;
  flex-direction: column;
}

.progress__title {
  width: 480px;
  color: @primary-black;
  font-size: 22px;
  font-weight: 400;
  line-height: 32px;
  margin-bottom: 25px;
}

.progress__gallery {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 10px;
}

.progress__gallery-image {
  margin-right: 30px;
  margin-bottom: 30px;
}

.progress__gallery-image:nth-child(4n) {
  margin-right: 0;
}

.progress__unfurl-link {
  .unfurl-link();
  position: relative;
  margin-bottom: 10px;
}

.progress__unfurl-icon {
.unfurl-icon()
}

.progress__unfurl-link:hover {
  .unfurl-link__hover();
}

/* temporary */

.progress__form-select {
  position: relative;
  width: 175px;
  border-radius: 4px;
  border: 1px solid @primary-grey;
  text-transform: capitalize;
  padding: 20px;
  margin-bottom: 40px;
  cursor: pointer;
  appearance: none;
  color: @primary-black;
  background-color: #ffffff;
}
