.pink-btn {
  width: 345px;
  font-size: 18px;
  font-weight: 400;
  line-height: 19px;
  letter-spacing: 0.18px;
  text-decoration: none;
  padding: 25px 60px;
  border-radius: 35px;
  color: #fffefe;
  background-color: #e94646;
