}
.pink-btn__hover {
  opacity: 0.9;
  color: #000000;
  box-shadow: 0 2px 5px #000000;
}
.light-btn {
  width: 345px;
  font-size: 18px;
  font-weight: 400;
  line-height: 19px;
  letter-spacing: 0.18px;
  text-decoration: none;
  padding: 25px 20px;
  border-radius: 35px;
  align-self: center;
  text-align: center;
  color: #e94646;
  background-color: #fff0f0;
  transition: background-color 0.7s 0.1s, color 0.5s 0.4s, box-shadow 0.5s 0.1s;
}
.light-btn__hover {
